.carousel {
  position: relative;
  display: grid;
  height: 100vh;
  background-color: inherit;
  align-content: center;
  justify-items: center;
}
.carousel * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}
.carousel--loading .control,
.carousel--loading .indicator {
  opacity: 0;
}
.carousel--loading .loading-screen {
  opacity: 1;
}

.slide {
  width: 100%;
  height: 100%;
}

.slide__item {
  font-size: 40px;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: 1.5s;
  opacity: 0;
  color: #fff;
}
.slide__item img {
  width: 100%;
  height: 100%;
  pointer-events: none;
  object-fit: cover;
}
.slide__item--active {
  z-index: 1;
  opacity: 1;
}

.control {
  font-size: 3rem;
  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  width: 90%;
  transition: all 1s ease-out 1s;
  opacity: 1;
  color: white;
  margin-inline: auto;
  align-items: center;
}
.control__item {
  display: grid;
  width: 90%;
  align-items: center;
  margin-inline: auto;
}
.control__item div * {
  font-size: 3rem;
  max-width: 10rem;
  max-height: 10rem;
  padding: 2rem;
  cursor: pointer;
  text-shadow: 0 0 1px black, 0 0 2px black, 0 0 3px black, 0 0 4px black, 0 0 5px #fff, 0 0 20px #fff;
}
.control .control-prev div,
.control .control-next div,
.control .control-pause div {
  transition: all 0.5s ease-out;
  opacity: 0.5;
}
.control .control-prev div:hover,
.control .control-next div:hover,
.control .control-pause div:hover,
.control .control-pause .control-play,
.control .control__item .handleAnimation {
  transform: scale(1.5);
  opacity: 1;
}
.control .control-prev div {
  justify-self: start;
}
.control .control-next div {
  justify-self: end;
}
.control .control-pause div {
  justify-self: center;
}

.indicator {
  position: absolute;
  z-index: 2;
  bottom: 10px;
  display: flex;
  width: 100%;
  transition: all 1s ease-out 1s;
  opacity: 1;
  justify-content: center;
}
.indicator__item {
  z-index: 3;
  width: 20px;
  height: 20px;
  margin: 0 5px;
  cursor: pointer;
  transition: transform 0.5s ease-out;
  transform: scale(1);
  opacity: 0.5;
  border: 2px solid black;
  border-radius: 50%;
  box-shadow: 0 0 10px #fff, 0 0 20px #fff;
}
.indicator__item:hover {
  transform: scale(1.25);
}
.indicator__item--active {
  transform: scale(1.25);
  background-color: black;
}

.loading-screen {
  font-size: 2rem;
  position: absolute;
  z-index: 100;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  transition: opacity 0.5s ease-out;
  opacity: 0;
  color: white;
  background-color: black;
  align-items: center;
  justify-content: center;
}
.loading-screen i {
  font-size: 36px;
  animation: rotate 2s linear infinite;
  color: white;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@media (min-width: 320px) {
  .control__item div * {
    font-size: 1rem;
  }
}
@media (min-width: 480px) {
  .control__item div * {
    font-size: 1.5rem;
  }
}
@media (min-width: 640px) {
  .control__item div * {
    font-size: 1.8rem;
  }
}
@media (min-width: 768px) {
  .control__item div * {
    font-size: 2rem;
  }
}
@media (min-width: 1024px) {
  .control__item div * {
    font-size: 3rem;
  }
}
@media (min-width: 1440px) {
  .control__item div * {
    font-size: 4rem;
  }
}